.App {
  height: 100vh; }

.App .username-wrapper {
  margin-left: 35px; }

.App .username {
  -webkit-transition: all 100ms ease-in-out;
  -o-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
  cursor: pointer;
  border-top: 1px solid #0000;
  border-bottom: 1px dotted #eee; }

.App .username:hover {
  color: #fff; }

.App .username-wrapper input {
  width: 250px; }

.App .color-wrapper {
  margin-left: 35px; }

.App .color {
  margin-left: 10px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  border-radius: 3px; }



